import {React } from 'react';
// import PropTypes from 'prop-types';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

const CookieGDRP = ({ show, onHide }) => {

  return (     
        <Modal show={show} onHide={onHide} centered>
        <Modal.Header closeButton={false}>
        {/* <Modal.Header closeButton> */}
            <Modal.Title>Informaii despre Cookies</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <div style={{ display: 'flex', alignItems: 'flex-start', marginTop: 0 }}>
            <div>
                <h6>Nou ne pas ca datele tale personale s rmn confideniale</h6>
                <p>
                    Pe acest site folosim un singur cookie funcional necesar autentificrii. 
                    Autentificarea se realizeaz prin intermediul formularului de login i implic consimmntul pentru utilizarea acestui cookie necesar.
                </p>
            </div>
            </div>
        </Modal.Body>
        <Modal.Footer>
            <Button variant="secondary" onClick={onHide}>
                Am ineles
            </Button>
            {/* <Button variant="primary" onClick={handleSave}>
                Save Changes
            </Button> */}
        </Modal.Footer>
        </Modal>
  );
};

export default CookieGDRP;
